.Form{width:385px;margin-left:auto;margin-right:auto}@media screen and (max-width: 768px){.Form{width:100%}}.Form__field{display:block;box-sizing:border-box;width:100%;min-height:54px;font-family:"Roboto",sans-serif;font-weight:300;font-size:1.6em;padding-left:20px;padding-right:20px;border:1px solid rgba(0,0,0,.5);border-radius:6px;margin-bottom:15px}@media screen and (max-width: 480px){.Form__field{margin-bottom:10px;min-height:44px;padding-left:15px;padding-right:15px;font-size:1.4em}}.Form__field::placeholder{color:rgba(0,0,0,.5)}.Form__field:focus{outline:none}.Form__select{background-color:#fff;padding:0;text-align:left;color:rgba(0,0,0,.5);position:relative}.Form__select_active{border-radius:6px 6px 0 0}.Form__select-active{cursor:pointer;font-size:16px;padding-right:45px;padding-left:20px;min-height:54px;height:min-content;display:flex;align-items:center;position:relative}@media screen and (max-width: 480px){.Form__select-active{min-height:44px;padding:5px 40px 5px 15px;font-size:14px}}.Form__select-active_active{color:#000}.Form__select-active::after{content:"";display:block;width:14px;height:14px;position:absolute;top:20px;right:20px;background-image:url("../../../assets/images/svg/select-arrow.svg");background-repeat:no-repeat;background-size:contain;background-position:center}@media screen and (max-width: 480px){.Form__select-active::after{right:15px}}.Form__select-popup{position:absolute;right:-1px;top:54px;width:100%;background-color:#fff;border:1px solid rgba(0,0,0,.5);border-top:none;border-radius:0 0 6px 6px;display:none;z-index:5}.Form__select-popup_active{display:block}.Form__popup-item{cursor:pointer;height:54px;padding:0 20px;display:flex;align-items:center;column-gap:10px;border-top:1px solid rgba(0,0,0,.5)}@media screen and (max-width: 480px){.Form__popup-item{height:44px;padding-left:15px;padding-right:15px}}.Form__popup-item-marker{margin:0;height:18px;width:18px;border-radius:50%;border:1px solid rgba(0,0,0,.5);box-sizing:border-box;display:flex;justify-content:center;align-items:center}.Form__popup-item-marker_active{border:1px solid #734}.Form__popup-item-marker_active::after{content:"";display:block;width:8px;height:8px;border-radius:50%;background-color:#734}.Form__popup-item-text{color:rgba(0,0,0,.5);font-size:16px;line-height:1}.Form__popup-item-text_active{color:#000}@media screen and (max-width: 480px){.Form__popup-item-text{font-size:14px}}.Form__button{width:100%;margin-top:20px}@media screen and (max-width: 768px){.Form__button{margin-top:15px}}@media screen and (max-width: 480px){.Form__button{margin-top:12px;margin-bottom:40px;position:relative}.Form__button::after{content:"При нажатии на кнопку вы соглашаетесь с условиями";display:block;width:70%;position:absolute;top:56px;font-family:"Roboto",sans-serif;color:#000;font-size:12px;text-transform:none}}.Form__checkbox-wrapper{margin-top:15px;display:flex;column-gap:8px;justify-content:left;align-items:flex-start}@media screen and (max-width: 768px){.Form__checkbox-wrapper{margin-top:12px}}@media screen and (max-width: 480px){.Form__checkbox-wrapper{display:none}}.Form__checkbox{margin:0;width:20px;height:20px;border:1px solid rgba(0,0,0,.5);border-radius:2px;cursor:pointer}.Form__checkbox_checked{border:1px solid #734;background-color:#734;background-image:url("../../../assets/images/svg/checkbox.svg");background-repeat:no-repeat;background-position:center}.Form__checkbox-label{font-size:1.4em;font-weight:300;text-align:left;width:min-content;flex-grow:1}